<template lang="html">
  <div>
    <article>
      <!--Details-->
      <div>

      </div>
      <!--Question and image perhaps-->
      {{question}}
      <div>

      </div>
      <!--Choices-->
      <div>
        <span
          v-for="index in 4"
          @click="submit(index-1)"
          v-bind:remarks="remarks[index-1]"
          :key="index">
          {{choices[temp[index-1]]}}
        </span>
      </div>
      <!--Answered-->
      <div v-show="temp.length==5">
        <span>Explanation: {{explanation}}</span>
        <span>Links: {{links}}</span>
      </div>
      <!-- nav -->
      <footer>
        <div>Previous</div>
        <span v-bind:title="'Contributed by: '+contributor">{{(stamp || 0)}}</span>

        <div>Next</div>

      </footer>
    </article>
  </div>
</template>

<script>
export default {
  name:'mcq',
  data:function(){
    return{
      bank:[],

/*
      token:{
        stamp:{},
        question:'',
        choices:'',
        image:'',
        links:'',
        explanation:'',
        contributor:'',
        temp:[1,2,3,4],
      },
*/
      index:0,
      remarks:['correct','wrong','null','null'],

      question:'This is a question placeholder',
      stamp:'',
      choices:['Choice A', 'Choice B', 'Choice C', 'Choice D'],
      links:'',
      explanation:'',
      contributor:'JB Ladiero',
      temp:[1,2,3,0]
    }
  },
  methods:{
    load:function(index){
      var token = this.bank[index];
      // Quick Reset
      //this.remarks.map( v => { v='null' } );
      // Shuffle
      this.choices = token.question.split('[~]');


    },
    submit:function(index){
      if(this.temp.length!=4){
        return; // Already answered
      }
      this.temp.push(index);
      // Paint
      if(this.temp[index]==0){
        this.remarks[this.temp]='correct';
      }else{
        this.remarks[index]='wrong';
        this.temp.map((v,i)=>{
          if(v==0) this.remarks[i]=='correct';
        })
      }
    },

  }
}
</script>

